@import "src/styles/variables";
@import "src/styles/mixins";


.mainBlock {
  width: 100%;
  margin-top: 100px;
  margin-bottom: 100px;
  //background-color: #fff;
  //margin-top: -4px;
  //overflow: visible;

  .titleWrapper{
    margin-bottom: 25px;
  }

  .mainBlockCont {
    width: $bigBlockForLW;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    //background-color: #bfb0f8;
    //height: 300px;
  }

  a{
    border-radius: 255px 15px 225px 15px/15px 235px 5px 275px;
    display: block;
    width: $smallBlockForLW;
    height: 170px;
    padding: 40px 45px;
    text-decoration: none;
    margin: 35px auto;
    background: rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border: 2px solid white;

    .flex{
      display: flex;
      flex-direction: column;

      div{
        width: 100%;
        height: 100%;
        //background-color: #c6fff6;
      }
    }

    .top{
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .course{
        @include text(20px, $primaryFont, 400);
        color: $darkColor;
      }

      .method{
        @include text(22px, $primaryFont, 500);
        color: #717171;
      }
    }

    .middle{
      margin-top: 5px;

      h4{
        @include text(32px, $primaryFont, 800);
        color: $darkColor;
        margin: 0;
      }
    }

    .bottom{
      margin-top: 55px;
      @include text(22px, $primaryFont, 600);
      color: #717171;

      .time{
        margin-right: 70px;
      }
    }
  }


  @media screen and (max-width: $slW){
    margin-top: 50px;
    margin-bottom: 50px;

    .titleWrapper{
      margin-bottom: 15px;
    }

    .mainBlockCont {
      width: $bigBlockForsLW;
    }

    a{
      width: $smallBlockForsLW;
      height: 130px;
      padding: 40px 45px;
      text-decoration: none;
      margin: 25px auto;

      .top{
        .course{
          @include text(18px, $primaryFont, 400);
          color: $darkColor;
        }

        .method{
          @include text(18px, $primaryFont, 500);
          color: #717171;
        }
      }

      .middle{
        margin-top: 2px;

        h4{
          @include text(28px, $primaryFont, 800);
        }
      }

      .bottom{
        margin-top: 35px;
        @include text(18px, $primaryFont, 600);

        .time{
          margin-right: 70px;
        }
      }
    }

  }


  @media screen and (max-width: $mW){
    //margin-top: 50px;
    //margin-bottom: 50px;

    //.titleWrapper{
    //  margin-bottom: 15px;
    //}

    .mainBlockCont {
      width: $bigBlockForMW;
    }

    a{
      width: $smallBlockForMW;
      height: 100px;
      padding: 30px 35px;
      text-decoration: none;
      margin: 25px auto;

      .top{
        .course{
          @include text(15px, $primaryFont, 400);
        }

        .method{
          @include text(15px, $primaryFont, 500);
        }
      }

      .middle{
        margin-top: 2px;

        h4{
          @include text(22px, $primaryFont, 800);
        }
      }

      .bottom{
        margin-top: 25px;
        @include text(16px, $primaryFont, 600);

        .time{
          margin-right: 70px;
        }
      }
    }

  }
}